import React, { useState } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { Box, TextField, Typography, Button, MenuItem, Checkbox, FormControlLabel, Snackbar, Alert, Stepper, Step, StepLabel } from "@mui/material";
import { applyToContest } from "../services/contestService";

const ContestApplication = () => {
  const { contestId } = useParams();
  const navigate = useNavigate();
  const [form, setForm] = useState({
    full_name: "",
    email: "",
    chair: "",
    description: "",
    address: "",
    city: "",
    country: "",
    termsAccepted: false,
  });
  const [step, setStep] = useState(0);
  const [submitting, setSubmitting] = useState(false);
  const [snackbar, setSnackbar] = useState({ open: false, message: "", severity: "success" });

  const handleChange = (e) => {
    const { name, value, checked, type } = e.target;
    setForm((prev) => ({ ...prev, [name]: type === "checkbox" ? checked : value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!form.termsAccepted) {
      setSnackbar({ open: true, message: "Please accept the terms to continue", severity: "warning" });
      return;
    }
    setSubmitting(true);
    try {
      await applyToContest(contestId, {
        full_name: form.full_name,
        email: form.email,
        chair: form.chair,
        description: form.description,
        address: form.address,
        city: form.city,
        country: form.country,
        terms_accepted: form.termsAccepted,
      });
      setSnackbar({ open: true, message: "Application submitted successfully!", severity: "success" });
      setTimeout(() => navigate(`/contests/${contestId}`), 1500);
    } catch (err) {
      setSnackbar({ open: true, message: err.message || "Failed to submit", severity: "error" });
    } finally {
      setSubmitting(false);
    }
  };
  const handleNext = () => setStep((s) => s + 1);
  const handleBack = () => setStep((s) => s - 1);

  const stepValid = () => {
    if (step === 0) return form.full_name && form.email && form.address && form.city && form.country;
    if (step === 1) return form.chair;
    if (step === 2) return form.termsAccepted;
    return false;
  };

  return (
    <Box sx={{ maxWidth: 700, mx: "auto", px: 2, py: 4, mt: { xs: 8, md: 10 } }}>
      <Typography variant="h4" sx={{ mb: 3, fontFamily: '"Bebas Neue", sans-serif' }}>
        Contest Application
      </Typography>

      <Stepper activeStep={step} alternativeLabel sx={{ mb: 3 }}>
        {['Your Info', 'Choose Chair', 'Terms'].map((label) => (
          <Step key={label}>
            <StepLabel>{label}</StepLabel>
          </Step>
        ))}
      </Stepper>

      <Box component="form" onSubmit={handleSubmit} sx={{ display: "flex", flexDirection: "column", gap: 2 }}>
        {step === 0 && (
          <>
            <TextField
              label="Full Name"
              name="full_name"
              value={form.full_name}
              onChange={handleChange}
              required
              fullWidth
            />
            <TextField
              label="Email"
              name="email"
              type="email"
              value={form.email}
              onChange={handleChange}
              required
              fullWidth
            />
            <TextField
              label="Street Address"
              name="address"
              value={form.address}
              onChange={handleChange}
              required
              fullWidth
            />
            <TextField
              label="City"
              name="city"
              value={form.city}
              onChange={handleChange}
              required
              fullWidth
            />
            <TextField
              label="Country"
              name="country"
              value={form.country}
              onChange={handleChange}
              required
              fullWidth
            />
          </>
        )}

        {step === 1 && (
          <>
            <TextField
              label="Select Chair"
              name="chair"
              select
              value={form.chair}
              onChange={handleChange}
              required
              fullWidth
            >
              {[
                { value: "X7", label: "X7" },
                { value: "E3", label: "E3" },
                { value: "P2", label: "P2" },
              ].map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </TextField>
          </>
        )}

        {step === 2 && (
          <FormControlLabel
            control={<Checkbox name="termsAccepted" checked={form.termsAccepted} onChange={handleChange} />}
            label="I accept the contest terms and conditions"
          />
        )}

        <Box sx={{ display: 'flex', justifyContent: 'space-between', mt: 2 }}>
          <Button disabled={step === 0} onClick={handleBack}>Back</Button>
          {step < 2 ? (
            <Button variant="contained" disabled={!stepValid()} onClick={handleNext}>
              Next
            </Button>
          ) : (
            <Button type="submit" variant="contained" disabled={submitting || !stepValid()}>
              {submitting ? 'Submitting...' : 'Submit Application'}
            </Button>
          )}
        </Box>
      </Box>

      <Snackbar
        open={snackbar.open}
        autoHideDuration={4000}
        onClose={() => setSnackbar((prev) => ({ ...prev, open: false }))}
        anchorOrigin={{ vertical: "bottom", horizontal: "center" }}
      >
        <Alert severity={snackbar.severity} sx={{ width: "100%" }}>
          {snackbar.message}
        </Alert>
      </Snackbar>
    </Box>
  );
};

export default ContestApplication;
